import styles from '../../styles/bars/headbar.module.css'
import Link from 'next/link'
import Image from 'next/image'

const Headbar = (props: { config: any }) => {
	const { config } = props

	const menu = (
		<div className={styles.menu}>
			{config?.menu?.map((ctrl: any, key: any) => {
				return (
					<Link
						style={{
							textDecoration: 'none',
							fontWeight: '500',
						}}
						key={key}
						href={ctrl.src.push}>
						{ctrl.label}
					</Link>
				)
			})}
		</div>
	)

	const access = (
		<div className={styles.access}>
			<Image
				width={24}
				height={24}
				src={'/icons/access.svg'}
				alt={'access'}></Image>
			<Link style={{ textDecoration: 'none' }} href={config?.port?.src.push}>
				{config?.port?.label}
			</Link>
		</div>
	)

	const brand = (
		<Image
			width={180}
			height={42}
			src={'https://svgshare.com/i/17P8.svg'}
			alt={'tmwgc_brand'}></Image>
	)

	const burger = (
		<Image
			className={styles.burger}
			width={24}
			height={24}
			src={'/icons/burger.svg'}
			alt={'burger'}></Image>
	)

	const shift = (
		<div className={styles.shift}>
			<p>Overall</p>
		</div>
	)

	const swap = (
		<div>
			<Image
				width={24}
				height={24}
				src={'/icons/domain.svg'}
				alt={'domain'}></Image>
		</div>
	)

	const action = (
		<div className={styles.action}>
			<button>Become a partner</button>
		</div>
	)

	return (
		<div className={styles.head}>
			{burger}
			{brand}
			{shift}
			{menu}
			{swap}
			<p>Create</p>

			{action}
			{access}
		</div>
	)
}

export default Headbar
